import axios from '@/plugins/axios'
import url from './url.js'


const API = {}

// 获取教师
API.teacher = function(page,state,worker) {
	return axios.get(url.Teacher, {page: page,state: state, worker: worker})
}


// 获取职工
API.audit = function(data) {
	return axios.post(url.Audit, data)
}

export default API;    